import firebase from 'firebase/app'
import 'firebase/firestore'
import 'firebase/auth'

// firebase init goes here
const config = {
    apiKey: "AIzaSyAehVOiNa-yKecHm6fm0_laST6rTBvWyuk",
    authDomain: "vue-app-e5169.firebaseapp.com",
    databaseURL: "https://vue-app-e5169.firebaseio.com",
    projectId: "vue-app-e5169",
    storageBucket: "vue-app-e5169.appspot.com",
    messagingSenderId: "499385645796"
}
firebase.initializeApp(config)

// firebase utils
const db = firebase.firestore()
const auth = firebase.auth()
const currentUser = auth.currentUser

// date issue fix according to firebase
const settings = {
    timestampsInSnapshots: true
}
db.settings(settings)

// firebase collections
const usersCollection = db.collection('users')
const postsCollection = db.collection('posts')
const commentsCollection = db.collection('comments')
const likesCollection = db.collection('likes')

export {
    db,
    auth,
    currentUser,
    usersCollection,
    postsCollection,
    commentsCollection,
    likesCollection
}


